    .tabla-b.color-acento-contenido(data-aos="zoom-in")(alt='La tabla detalla los pasos en la metodologa Kaizen, divididos en tres fases: Planear, Hacer y Verificar-Actuar. En la fase de Planear, se identifican reas que requieren mejoras, se forman equipos KAIZEN, se recopilan datos y se analiza la situacin actual. Luego, se proponen soluciones y se planifican acciones especficas. En la fase de Hacer, se implementan las mejoras planificadas, documentando el proceso y utilizando herramientas como mapas de flujo de valor y anlisis de causa raz. Finalmente, en la fase de Verificar-Actuar, se evalan los resultados, se comparan con los objetivos y se estandarizan las prcticas efectivas para asegurar mejoras continuas.')
