        tr
          td.br-tabble.tab-boldoo 3
          td.br-tabble Identificar a los miembros del equipo KAIZEN
          td Nombrar lder de equipo, miembros de la misma rea y de diferente rea.   

        tr
          td.br-tabble.tab-boldoo 4
          td.br-tabble Completar hoja de preparacin del KAIZEN
          td Realizar guion del desarrollo del evento, debe ser aprobado por lder de rea.    

        tr
          td.br-tabble.tab-boldoo 5
          td.br-tabble Entender la situacin actual
