    p.mb-4 En cada una de las actividades que conforman el rea de trazo y corte se establecen controles para revisar. Es primordial contar con la trazabilidad de la produccin, para ubicar fcilmente cada lote, poder intervenir rpidamente en los productos a corregir y dar respuesta rpida para solucionar el inconveniente. 
    p.mb-2 Los documentos utilizados en el rea para controlar el corte, adems de la orden de produccin, son las inspecciones divididas en procesos segn la actividad a ejecutar. Los requisitos para revisar en cada actividad son los siguientes: 
    
    .row(data-aos="zoom-in")
      .col-lg-4.mb-2(data-aos="fade-left")
        figure.d-lg-block.d-none.my-3
          img.my-4(src='@/assets/ilustraciones/345.png',alt='tema5') 
      .col-lg-8.my-3
        AcordionA.activo(data-aos="flip-up")(tipo="b" clase-tarjeta="tarjeta tarjeta--grissss")
          .row.justify-content-center.align-items-center(titulo="Trazo")
            .col-lg-12.mb-4.mb-md-0
              .row.justify-content
                .col-lg-12
                  h4 Alineacin de piezas 
                  p Cada pieza debe estar contenida en el trazo conforme a la lnea de aplomo.  
                .col-lg-12
                  h4 Organizacin de piezas 
                  p Con el fin de controlar los rendimientos de telas, es importante la disposicin de las piezas, colocando una al lado de la otra y aprovechando lneas o curvas compartidas entre las piezas. 
                .col-lg-12
                  h4 Cantidad de piezas
                  p Contar el nmero de piezas que corresponda a cada talla del producto.                                
                .col-lg-12
                  h4 Ancho de trazo 
                  p Sea conforme el ancho til de la tela.  

          .row.justify-content-center.align-items-center(titulo="Tendido")
            .col-lg-12.mb-4.mb-md-0
              .row.justify-content-center.align-items-center
                .col-lg-12
                  h4 Alineacin de capas 
                  p Respetar la margen colocada a partir de la base, es decir la primera capa. Tambin en los extremos, alcance a cubrir el largo del trazo, sobre todo en trazos escalonados. 

                .col-lg-12
                  h4 Uniformidad de tendido 
                  p El extendido de cada capa no debe tener arrugas. 

                .col-lg-12
                  h4 Cantidad de capas 
                  p Antes de cortar se debe contar el nmero de capas, correspondan a las unidades a cortar segn distribucin de trazo. 

                .col-lg-12
                  h4 Tiempos de reposo 
                  p Sobre todo en tejidos de punto, para evitar encogimiento, se tiene en cuenta segn ficha tcnica de la tela y se valida con pruebas que confirmen niveles de encogimiento desarrollado una vez finalicen los tiempos de reposo. 

                .col-lg-12
                  h4 Lado de tela 
                  p Teniendo en cuenta el tipo de trazo, se debe respetar los derechos de la tela. 

                .col-lg-12
                  h4 Tela 
                  p Defectos de tela, marcar lo ms posible, piques, marras, entre otras. Cuidar la combinacin de rollos y de colores para evitar contaminacin entre los colores por los residuos de las fibras en el corte del tejido. 

          .row.justify-content-center.align-items-center(titulo="Corte")
            .col-lg-12.mb-4.mb-md-0
              .row.justify-content-center.align-items-center
                .col-lg-12
                  h4 Uniformidad 
                  p Respetar el trazo de cada pieza, obedeciendo a curvas y rectas segn sea el caso de cada parte de la pieza. 
                
                .col-lg-12
                  h4 Simetras 
                  p Corresponder las simetras de cada pieza sosteniendo el colchn o bloque de telas, evitando se corra para que no afecte las simetras de las capas inferiores.
                
                .col-lg-12
                  h4 Piquetes y puntos 
                  p Ubicacin de piquetes y en profundidad de piquetes no mayor de 0,4 cms; en los puntos evitar se corra la ubicacin, debe tener un tamao razonable.
                
                .col-lg-12
                  h4 Corte de piezas de forro y entretela
                  p Revisar el material y las piezas se corten conforme la ficha tcnica y la programacin de la produccin.                                      
          .row.justify-content-center.align-items-center(titulo="Tiqueteado")
            .col-lg-12.mb-4.mb-md-0
              .row.justify-content-center.align-items-center
                .col-lg-12
                  h4 Ubicacin 
                  p que se coloque por el revs de la tela en un lugar visible y que no afecte en el momento del ensamble.
                  
                .col-lg-12
                  h4 Descripcin 
                  p que la informacin corresponda a cada pieza, debe contener el nmero consecutivo de la pieza en orden, segn el tendido, adems incluir talla y referencia.                   

          .row.justify-content-center.align-items-center(titulo="Fusionado")
            .col-lg-12.mb-4.mb-md-0
              .row.justify-content-center.align-items-center
                .col-lg-12
                  h4 Temperatura 
                  p Que corresponda al material a fusionar. 

                .col-lg-12
                  h4 Tiempo 
                  p Duracin en la fusionadora.
                  
                .col-lg-12
                  h4 Presin 
                  p Ejercida en cada pieza para que la unin sea uniforme.                  

                .col-lg-12
                  h4 Alineacin y posicin 
                  p Que se encuentre derecha y teniendo en cuenta si se debe dejar el ancho de costura.
                  
                .col-lg-12
                  h4 Limpieza de la mquina 
                  p Para evitar manchas en las prendas fusionadas.

          .row.justify-content-center.align-items-center(titulo="Paqueteado")
            .col-lg-12.mb-4.mb-md-0
              .row.justify-content-center.align-items-center
                .col-lg-12
                  h4 Cantidad 
                  p Unidades de acuerdo con lo determinado para incorporar a la produccin, 10 o 20, se prefiere paquetes pequeos que facilitan el manejo, o ms unidades segn lo establecido por la empresa.           

                .col-lg-12
                  h4 Amarre 
                  p En el doblado de las piezas deben estar incluidas todas las piezas del paquete y con un fuerte amarre.
                  
                .col-lg-12
                  h4 Identificacin 
                  p Rotulado de cada paquete con tarjetas que indique la informacin necesaria como talla, nmero de lote, cantidad, entre otras que precisen el contenido del paquete.
    
